*,
*:before,
*:after {
  box-sizing: border-box;
}

body{
  color: white;
}

.container {
  max-width: 1530px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

h1, h2 {
  margin: 0;
}

ol,
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  display: block;
}

